.container {
  position: absolute;
  top: 0px;
  left: 0px;
  flex-grow: 1;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgba(var(--background1-rgb), 0.233);
  z-index: 999;
}

.label {
  padding: 20px;
  border: 1px solid var(--background1);
  background-color: var(--foreground6);
  text-transform: uppercase;
  color:var(--background1);
}
