@import "~@origam/styles/dist/styles/EditorsCommon.module";

.root {
  position: absolute;
}

.caption {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  overflow: hidden;
  padding: 0 0.5em 0 0;
  white-space: pre-wrap;
  word-wrap: normal;
}

.editor {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}


.notification {
  @include notification;
}
