@import "../../../../../../node_modules/@origam/styles/dist/styles/EditorsCommon.module";
@import "../../../../../../node_modules/@origam/styles/dist/styles/commonStyles";
@import "../../../../../../node_modules/@origam/styles/dist/styles/definitions";

.droppedPanelContainer {
  z-index: 1000;
  background-color:var(--background1);
  overflow: hidden;
  @include popoverShadow;
  width: 224px;

  box-shadow: 0 0.5em 1.5em 0 rgba(var(--background8-rgb), 0.25);
  border: 1px solid var(--background5);
  @include border-radius(4px);
}

.dropdownSymbol {
  @include dropdownSymbol;
}

.input {
  @include input;
}

.notification {
  @include notification;
}

.editorContainer {
  @include editorContainer;
}


.formatHintTooltip {
  padding: 5px;
  position: fixed;
  border: 1px solid var(--background8);
  background-color:var(--background1);
  z-index: 5000;
}